import React from 'react';

function StreamlitPage() {
  return (
    <div style={{ height: '100vh', width: '100%' }}>
      <iframe
        src="http://localhost:8501"
        style={{ width: '100%', height: '100%', border: 'none' }}
        title="Streamlit App"
      />
    </div>
  );
}

export default StreamlitPage;
