import React from "react";
import { useState, useEffect } from "react";
import { IoSearch } from "react-icons/io5";
import axios from "axios";
import Jobitem from "./Job_item";
import ApplyModal from "./ApplyModal";
import Config from "../config/Config.json";

let jobsData = [];
const Jobs = () => {
  const [modal, setModal] = useState(false);
  const [action, setAction] = useState(false);
  const [jobSet, setjobSet] = useState("");
  const [jobs, setJobs] = useState([]);

  const closeModalHandler = () => {
    setModal(false);
  };

  const jobApply = (applyData) => {
    setModal(true);
    setjobSet(applyData);
  };

  const jobSearchHandler = (event) => {
    event.preventDefault();
    setJobs(
      jobsData.filter((job) =>
        job.title.toLowerCase().includes(event.target.value.toLowerCase())
      )
    );
  };

  useEffect(() => {
    axios
      .get(`${Config.SERVER_URL + "user/jobsAvailable"}`, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        jobsData = response.data.jobs;
        setJobs(response.data.jobs);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [action]);

  return (
    <>
      <div 
      style={{
              backgroundColor: "#f5f5f5",
              backgroundImage: `url(https://img.freepik.com/free-photo/coffee-cup-laptop-black-table-top-view-copyspace-background_144627-24861.jpg?semt=ais_hybrid)`,
              backgroundSize: `cover`,
          }}> 
        <div
          className="h-60"
           
        >
           <div className="text-center text-base mx-24 max-sm:mx-2">
            <div className="bg-white bg-opacity-10 absolute top-1/4 w-10/12 rounded-full my-2">
              <IoSearch className="relative text-white top-10 ml-10 h-6 w-6" />
              <input
                type="text"
                onChange={jobSearchHandler}
                className="w-10/12 border-transparent relative bottom-1 text-white text-1xl mx-4 px-4 max-sm:w-10/12 bg-transparent bg-opacity-0 mb-7"
              />
            </div>
          </div>
        </div>    
        <div >
          <div className="mx-24 max-sm:mx-0">
            <div className="block columns-1">
              {jobs.map((jobItem) => (
                <Jobitem key={jobItem._id} item={jobItem} jobApply={jobApply} />
              ))}
              {modal && (
                <ApplyModal
                  job={jobSet}
                  onOpen={modal}
                  onClose={closeModalHandler}
                  changes={setAction}
                />
              )}
            </div>
          </div>
        </div>
        </div>
        </>
  );
};

export default Jobs;
